<template>
    <div>
        <div>选择难度</div>
        <select v-model="level" @click="update">
            <option value="low">low</option>
            <option value="medium">medium</option>
            <option value="high">high</option>
            <option value="user_defined">自定义</option>
        </select>
        <div v-if="level == 'user_defined'">
                <div>row:</div>
            <input type="number" v-model="m" @change="update">
            <div>col:</div>
            <input type="number" v-model="n" @change="update">
        </div>
    </div>
        
</template>

<script setup>
import { useStore } from 'vuex';
import { ref,computed } from 'vue';
const store = useStore();
const m = ref(store.state.row);
const n = ref(store.state.col);
const level = computed(() => {return store.state.level});
const levels=store.state.levels;
const update = () => {
    if (levels.has(level)){
        store.commit("update", [levels.get(level),level]);

    }
    else store.commit("update", [m, n,level]);
}

</script>

<style scoped></style>
